import React, { useState } from "react";

function ItemList(props) {
  const [isStrike, setIsStrike] = useState(false);

  function updateSetIsStrike() {
    isStrike ? setIsStrike(false) : setIsStrike(true);
  }

  return (
    <li>
      <button
        onClick={() => {
          props.onclick(props.id);
        }}
      >
        Delete
      </button>
      <span>&nbsp;&nbsp;</span>
      <span
        style={{ textDecoration: isStrike ? "line-through" : null }}
        onClick={updateSetIsStrike}
      >
        {props.itemName}
      </span>
    </li>
  );
}

export default ItemList;
