import React, { useState } from "react";
import ItemList from "./ItemList";
import * as uuid from "uuid";
import Footer from "./Footer";

function App() {
  const [newItem, setNewItem] = useState("");
  const [allItems, setAllItems] = useState([]);

  function updateSetNewItem(event) {
    setNewItem(event.target.value);
  }

  function updateSetAllItems() {
    setAllItems((preValue) => {
      console.log(preValue);
      return [...preValue, { name: newItem, id: uuid.v4() }];
    });
    setNewItem("");
  }

  function onClick(id) {
    setAllItems((preValue) => {
      return preValue.filter((item, index) => {
        return index !== id;
      });
    });
  }

  return (
    <>
      <div className="container">
        <div className="heading">
          <h1>To-Do List</h1>
        </div>
        <div className="form">
          <input onChange={updateSetNewItem} value={newItem} type="text" />
          <button onClick={updateSetAllItems}>
            <span>Add</span>
          </button>
        </div>
        <div>
          <ul>
            {allItems.map((item, index) => (
              <ItemList
                key={item.id}
                id={index}
                itemName={item.name}
                onclick={onClick}
              />
            ))}
          </ul>
        </div>
      </div>
      <Footer />
    </>
  );
}

export default App;
